import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

//TODO:         ,       . 
//     .    ,    .
//       . 
//         

class MaterialSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			buttonId: null
		}
		this.setButton = this.setButton.bind(this);
		this.go = this.props.go.bind(this);
	}
    setButton(id){
		this.setState({buttonId: id});
	}
	render() {
		return(
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					 <strong></strong>
				</PanelHeader>
				<Group className="Group">
        	    	<Div>
        	    	    <Button className={this.state.buttonId === 1?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2" 
        	    	        onClick={() => this.setButton(1)}
        	    	    ></Button>
        	    	    <Button className={this.state.buttonId === 2?
        	    	        "logoButton" : "Button"} 
        	    	        stretched
        	    	        size="xl"
        	    	        level="2"
        	    	        onClick={() => this.setButton(2)}
        	    	    ></Button>
        	    	    <Button className={this.state.buttonId === 3?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(3)}
        	    	    ></Button>
        	    	    <Button className={this.state.buttonId === 4?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(4)}
        	    	    ></Button>
        	    	    <Button className={this.state.buttonId === 5?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(5)}
        	    	    ></Button>
        	    	    <Button className={this.state.buttonId === 6?
        	    	        "logoButton" : "Button"} 
        	    	        size="xl"
        	    	        stretched
        	    	        level="2"
        	    	        onClick={() => this.setButton(6)}
        	    	    ></Button>
					</Div>
        	        <List>
        	            <Cell multiline>
        	                {this.state.buttonId === 1 && <Div style={{backgroundColor: '#ffffff'}} > <hr/>, ,   .     ,   .</Div>}
        	                {this.state.buttonId === 2 && <Div style={{backgroundColor: '#ffffff'}} > <hr/>, ,  ,   .</Div>}
        	                {this.state.buttonId === 3 && <Div style={{backgroundColor: '#ffffff'}} > <hr/>,     ,     , , , , ,    .</Div>}
        	                {this.state.buttonId === 4 && <Div style={{backgroundColor: '#ffffff'}} > <hr/>100%    ,    20 .</Div>}
        	                {this.state.buttonId === 5 && <Div style={{backgroundColor: '#ffffff'}} > <hr/>     , , ,   .</Div>}
        	                {this.state.buttonId === 6 && <Div style={{backgroundColor: '#ffffff'}} > <hr/>  . , ,  . 100% .   .</Div>}
        	                {this.state.buttonId !== 1  &&
        	                    this.state.buttonId !== 2 &&
        	                    this.state.buttonId !== 3 &&
        	                    this.state.buttonId !== 4 &&
        	                    this.state.buttonId !== 5 &&
        	                    this.state.buttonId !== 6 && <Div style={{backgroundColor: '#ffffff'}} >   .</Div>}
        	            </Cell>
        	        </List>		
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="typeSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="viewSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}	
}

MaterialSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default MaterialSelection;
